<template>
    <div>
        <nav class="nav container">
            <a href="" class="logo">
                <img src="@/assets/images/logo.svg" alt="">
                <h1>vue weather</h1>
            </a>
            <div class="nav__right">
                <button>
                    <img src="@/assets/images/drop.svg" alt="">
                </button>
                <input type="text" placeholder="Введите город" v-model="city" @keydown.enter="getWeather(city)">
            </div>
        </nav>
    </div>
</template>
<script>
import { mapActions } from 'vuex'
export default {
    data() {
        return {
            city: ''
        }
    },
    methods: {
        ...mapActions(['getWeather'])
    }
}
</script>
