<template>
    <div class="container">
        <MainContent />
        
        <div class="main__btns">
            <button class="main__btns_btn" :class="{active: week}" @click="week = true">На неделю</button>
            <button class="main__btns_btn"  @click="week = false">Отменить</button>
        </div>
    <Transition name="week">
        <Week v-if="week"/>
    </Transition>
    </div>
</template>

<script>
import MainContent from './MainContent.vue';
import Week from './Week.vue';
export default {
    components: { MainContent, Week },
    data() {
        return {
            week: false
        }
    }
}
</script>
