<template>
    <div class="week__day">
        <h4 class="week__day_title">{{  getWeekDay }}</h4>
        <span>{{ getDay }} {{ getMonth }}</span>
        <img :src="getImg" alt="">
        <h4 class="week__day_title">+{{ Math.round(day.temp.max) }}°</h4>
        <span>+{{ Math.round(day.feels_like.day) }}°</span>
        <span>{{ description }}</span>
    </div>
</template>

<script>
import unix from '@/unix'
import { weatherName } from '@/icon.js'
export default {
    props: {
        day: Object
    },
    computed: {
        description() {
            return this.day.weather[0].description
        },
        getImg() {
            return weatherName[this.description] || weatherName['ясно']
        },
        getDay(){
            return unix(this.day.dt, 'day')
        },
        getMonth(){
            return unix(this.day.dt, 'month')
        },
        getWeekDay(){
            return unix(this.day.dt, 'weekDay')
        }
    }
}
</script>
