import {
  createStore
} from 'vuex'
import axios from 'axios'

export default createStore({
  state: {
    apiKey: '7867ab7c96ef450add72c11cac002515',
    weather: null
  },
  getters: {

  },
  mutations: {

    getWeather(state, payload) {
      state.weather = payload
    }
  },
  actions: {
    async getWeather(context, city) {
      try {
        const results = await axios.get(`http://api.openweathermap.org/geo/1.0/direct?q=${city}&appid=${context.state.apiKey}`)
        // console.log(results);

        const {
          lat,
          lon,
          local_names
        } = results.data[0]

        if (city.toLowerCase() == local_names.ru.toLowerCase()) {

          const results2 = await axios.get(`https://api.openweathermap.org/data/2.8/onecall?lat=${lat}&lon=${lon}&exclude=minutely,hourly,alerts&units=metric&lang=ru&appid=${context.state.apiKey}`)

          console.log(results2);
          const weatherInfo = {
            ...results2.data,
            name: city
          }

          context.commit('getWeather', weatherInfo)
        }

      } catch (error) {
        console.error(`Произошла ошибка в получении данных ${error}`);
      }


    }
  },

})