function unix(dt, type) {
    const seconds = dt * 1000
    const data = new Date(seconds)

    let results = type == 'day' ? data.toLocaleString('ru-RU', {
            day: "numeric"
        }) :
        type == 'month' ? data.toLocaleString('ru-RU', {
            month: "long"
        }) :
        type == 'weekDay' ? data.toLocaleString('ru-RU', {
            weekday: "long"
        }) :
        ''
        return results
}
export default unix